import React from 'react';
import { Card, ListGroup } from 'react-bootstrap';
import { getRandomColor } from '../../utilities/extensions';
import { AiFillBug } from "react-icons/ai";
import { Link } from '@primer/components';

function SkillCategoryCard(props) {
    return (
        <Card className="pad-xs">
            <div className="app-card-header">
                <span className="card-icon">
                    <AiFillBug></AiFillBug>
                </span>
                <span className="card-title">{props.category}</span>
            </div>
            <ListGroup variant="flush" className="mar-top-sm">
                {
                    props.subSkills.map((x, i) => {
                        return (
                            <ListGroup.Item key={i}>
                                <div className="language-mark" style={{ backgroundColor: getRandomColor() }}></div>
                                {x.title}
                            </ListGroup.Item>
                        );
                    })
                }
            </ListGroup>
        </Card>
    );
}

export default SkillCategoryCard;