import React from 'react';
import PropTypes from 'prop-types';
import { View, Text } from '@react-pdf/renderer';

function ResumeEducation(props) {
    return (
        <>
            <View style={props.styles}>
                <Text style={[props.titleStyle, { paddingBottom: 5, borderBottom: 1, borderBottomColor: "#2e2e2e", marginBottom: 5 }]}>
                    Education
                </Text>
                <View style={{ display: "flex", flexDirection: "row" }}>
                    <View style={{ display: "flex", flexDirection: "column" }}>
                        <Text style={[props.titleStyle]}>
                            {props.data.constitutionName}
                        </Text>
                        <Text style={[props.subHeadingStyle, { fontWeight: "bold" }]}>
                            {props.data.degreeName}{" in "}{props.data.fieldOfStudy}
                        </Text>
                    </View>
                    <View style={{ width: 500, display: "flex", flexDirection: "column", alignItems: "flex-end" }}>
                        <Text style={[props.titleStyle, { fontSize: 11 }]}>
                            Course Duration
                        </Text>
                        <Text style={[props.titleStyle, { fontSize: 11 }]}>
                            {props.data.startYear}{" - "}{props.data.endYear}
                        </Text>
                    </View>
                </View>
            </View>
        </>
    )
}

ResumeEducation.propTypes = {
    styles: PropTypes.object.isRequired,
    data: PropTypes.object.isRequired
};

export default ResumeEducation;

