import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { injectMarkdown } from '../../../content/markdown.provider';
import { appModule, markdownKeys } from '../../../utilities/enums';
import { fetchMarkdownData, getMarkdownContentByKey, covertToDate } from '../../../utilities/extensions';
import { View, Text } from '@react-pdf/renderer';

function ResumeExperience(props) {
    console.log(props.totalExperience);
    const [mdText, setMdText] = useState("");
    useEffect(() => {
        injectMarkdown(appModule.Organization, props.experience.organization.descriptionFile).then(res => {
            fetchMarkdownData(res.default).then(text => {
                setMdText(getMarkdownContentByKey(text, markdownKeys.Resume));
            });
        });
    }, [mdText]);
    console.log(mdText);
    return (
        <View style={{ display: "flex", flexDirection: "column" }}>
            <View style={{ display: "flex", flexDirection: "row", borderBottom: 1, borderBottomColor: "#2e2e2e" }}>
                <View>
                    <Text style={[props.styles.titleStyle]}>
                        {props.experience.organization.name}
                    </Text>
                    <Text style={[props.styles.subHeadingStyle]}>
                        {props.experience.organization.keyAreas[0]}
                    </Text>
                </View>
                <View style={{ width: 500, alignItems: "flex-end" }}>
                    <Text style={[props.styles.titleStyle, { fontSize: 11 }]}>
                        {props.experience.experience}
                    </Text>
                    <Text style={[props.styles.subHeadingStyle]}>
                        {covertToDate(props.experience.fromDate)} {" - "}
                        {(props.experience.isWorking) ? "Present" : covertToDate(props.experience.toDate)}
                    </Text>
                </View>
            </View>
            <View style={[props.styles.bodyStyle]}>
                <Text >
                    {mdText != "" ? mdText : " "}
                </Text>
            </View>
        </View>
    )
}

ResumeExperience.propTypes = {
    styles: PropTypes.object.isRequired,
    experience: PropTypes.object.isRequired,
    totalExperience: PropTypes.object.isRequired
};

export default ResumeExperience;

