import React from 'react';
import { Button } from 'react-bootstrap';
import { AvatarStack, Link } from '@primer/components';
import { MetaDataApi } from '../../data/app-data';
import { Link as AppLink } from 'react-router-dom';

function SocialContactWidget() {
    let socialContacts = MetaDataApi.getContactDetails();
    return (
        <>
            <i style={{ color: "#666", verticalAlign: "top" }}>
                Shout at &nbsp;
            </i>
            <AvatarStack>
                <Link href={socialContacts.github}>
                    <img alt="GitHub" src="https://avatars.githubusercontent.com/github" />
                </Link>
                <Link href={socialContacts.linkedIn}>
                    <img alt="LinkedIn" src="https://image.flaticon.com/icons/png/512/174/174857.png" />
                </Link>
                <Link href={"mailto:" + socialContacts.email}>
                    <img alt="Mail To" src="https://image.flaticon.com/icons/png/512/281/281769.png" />
                </Link>
            </AvatarStack>
            <div className="mar-top-sm">
                <AppLink to="/hire-me">
                    <Button variant="light" className="full-width">Hire Me!</Button>
                </AppLink>
            </div>
        </>
    );
}

export default SocialContactWidget;