import React, { useState } from 'react';
import { UnderlineNav } from '@primer/components';
import { Switch, Route, Link } from 'react-router-dom';
import OverviewPage from '../overview/overview.page';
import SkillPage from '../skills/skill-page';
import RepositoryPage from '../repository/repository.page';
import ExperiencePage from '../experience/experience.page';
import { Nav } from 'react-bootstrap';

function DashboardPage(props) {
    const [defaultKey, setDefaultKey] = useState(window.location.pathname);
    const getSelectedTab = function (eventKey) {
        setDefaultKey(eventKey);
    }
    return (
        <>
            <UnderlineNav>
                <Link to="/overview"  >
                    <UnderlineNav.Link as="div" onClick={() => getSelectedTab("/overview")} selected={defaultKey === "/overview"}>
                        Overview
                        </UnderlineNav.Link>
                </Link>
                <Link to="/skills">
                    <UnderlineNav.Link as="div" onClick={() => getSelectedTab("/skills")} selected={defaultKey === "/skills"}>
                        Skills
                        </UnderlineNav.Link>
                </Link>
                <Link to="/experience">
                    <UnderlineNav.Link as="div" onClick={() => getSelectedTab("/experience")} selected={defaultKey === "/experience"}>
                        Experiences
                        </UnderlineNav.Link>
                </Link>
                <Link to="/repositories">
                    <UnderlineNav.Link as="div" onClick={() => getSelectedTab("/repositories")} selected={defaultKey === "/repositories"}>
                        Repositories
                        </UnderlineNav.Link>
                </Link>
            </UnderlineNav>
            <Switch>
                <Route path="/overview">
                    <OverviewPage />
                </Route>
                <Route path="/experience">
                    <ExperiencePage />
                </Route>
                <Route path="/repositories">
                    <RepositoryPage />
                </Route>
                <Route path="/skills">
                    <SkillPage />
                </Route>
            </Switch>
        </>
    );
}

export default DashboardPage;