import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { SkillApi } from '../../data/app-data';
import SkillCategoryCard from './skill-category-card';

function SkillPage() {
    const skillCategories = SkillApi.getSkillsByCategories().sort(x => x.subSkills.length).sort(function (a, b) {
        // ASC  -> a.length - b.length
        // DESC -> b.length - a.length
        return a.subSkills.length - b.subSkills.length;
    });

    return (
        <Container className="mar-top-md skill-page">
            <Row>
                {skillCategories.map((x, i) => {
                    return (
                        <Col md={6} sm={12} className="app-card" key={i}>
                            <SkillCategoryCard {...x} key={i} />
                        </Col>);
                })}
            </Row>
        </Container>
    );
}

export default SkillPage;
