import React from 'react';
import PropTypes from 'prop-types';
import { View, Text, StyleSheet } from '@react-pdf/renderer';

function ResumeSkills(props) {
    console.log(props.data);
    return (
        <View style={props.styles}>
            <Text style={[props.titleStyle]}>
                Pinned Skills
            </Text>
            {
                props.data.map((val, idx) => {
                    return (
                        <View style={{ display: "flex", flexDirection: "row" }} key={idx}>
                            <Text style={[props.tableHeaderStyle, { width: "130" }]}>
                                {val.category}
                            </Text>
                            <View style={[props.tableBodyStyle, { width: "450" }]}>
                                {val.subSkills.map((skill, idx) => {
                                    return <Text key={idx} style={[props.skillBadge]}>
                                        {skill.title}
                                    </Text>;
                                })}
                            </View>
                        </View>
                    );
                })
            }
        </View>
    )
}

ResumeSkills.propTypes = {
    styles: PropTypes.object.isRequired,
    data: PropTypes.array.isRequired
};

export default ResumeSkills;

