import React from 'react';
import { Navbar } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { MetaDataApi } from '../../data/app-data';
import { ProfileLogoLight } from '../../assets/assets.provider';

function TopNavWidget() {
    let personData = MetaDataApi.getPersonData();
    return (
        <Navbar variant="dark" className={"nav-color justify-content-center"}>
            <Navbar.Brand>
                <Link to="/overview">
                    <img
                        src={ProfileLogoLight}
                        width="40"
                        height="40"
                        className="d-inline-block align-top"
                        alt={personData.name + " | " + personData.role}
                    />
                </Link>
            </Navbar.Brand>
        </Navbar>
    );
}

export default TopNavWidget;