import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { injectMarkdown } from '../../content/markdown.provider';
import { appModule, markdownKeys, resumeRole } from '../../utilities/enums';
import { fetchMarkdownData, getMarkdownContentByKey } from '../../utilities/extensions';
import { PDFViewer } from '@react-pdf/renderer';
import { Container, Row, Col } from 'react-bootstrap';
import HireConnectWidget from './hire_connect.widget';
import ResumeDocument from './resume/resume.document';

function HireMePage(props) {
    const resumeMetaData = {
        roleId: resumeRole.FullStackDeveloperDotNet
    }

    return (
        <Container>
            <Row>
                <Col md={12} className="mar-bottom-sm">
                    <HireConnectWidget />
                </Col>
                <Col md={12}>
                    <small style={{ color: "red" }}><i>Please download Cover Letter for now. This resume miss few information's.</i></small>
                    <PDFViewer className="resume-iframe">
                        <ResumeDocument {...resumeMetaData} />
                    </PDFViewer>
                </Col>
            </Row>
        </Container>
    )
}

HireMePage.propTypes = {

};

export default HireMePage;

